.Header {
    background-image: url("../../images/header.png");
    background-position: top;
    width: 1920px;
    height: 960px;

}

.pictures {
    width: 1920px;
    height: 960px;
}

@media screen and (max-width: 768px) {
    .Header {
        width: 768px;
        height: 1024px;

    }

}
@media screen and (max-width: 375px) {
    .Header {
        width: 375px;
        height: 596px;

    }

}
@media screen and (max-width: 320px) {
    .Header {
        width: 320px;
        height: 509px;

    }

}
